.management-left {
  width: 20%;
}

.management-left > div:nth-of-type(1) {
  overflow: hidden;
  width: 100%;
  height: 36vh;
  background-color: #082572;
  text-align: center;
}

.management-left > div:nth-of-type(1) > p:nth-of-type(1) {
  font-size: 2em;
  color: #fff;
  margin-top: 2em;
}

.management-left > div:nth-of-type(1) > div:nth-of-type(1) {
  display: flex;
  justify-content: space-around;
}

.management-left > div:nth-of-type(1) > div:nth-of-type(1) > div {
  color: #fff;
  display: flex;
  align-items: center;
  margin: 0 auto;
}

.management-left > div:nth-of-type(1) > div:nth-of-type(1) > div img {
  width: 3em;
  height: 3em;
  display: block;
  border-radius: 50%;
}

.management-left > div:nth-of-type(1) > div:nth-of-type(1) > div > p {
  margin: 0;
  font-size: 1.6em;
  margin-left: 1em;
}

.management-left > div:nth-of-type(1) > p:nth-of-type(2) {
  font-size: 1.5em;
  color: #fff;
  margin-top: 1em;
}

.management-left > div:nth-of-type(2) {
  width: 100%;
  height: calc(64vh - 1em);
  margin-top: 1em;
  background-color: #082572;
  overflow: auto;
}

.ant-menu {
  background-color: #082572 !important;
  color: rgba(255, 255, 255, 0.65) !important;
}

.ant-menu i {
  color: rgba(255, 255, 255, 0.65) !important;
}

.ant-menu-sub.ant-menu-inline {
  background-color: #082572 !important;
}

.ant-menu-item {
  background-color: #082572 !important;
}

.ant-menu:not(.ant-menu-horizontal) .ant-menu-item-selected {
  background: none !important;
  color: #C4861F !important;
}

.ant-menu-submenu-selected {
  color: #fff !important;
}

.ant-menu-item-active {
  color: #fff !important;
}

.ant-menu-submenu-active {
  color: #fff !important;
}

.management-right {
  width: 80%;
  font-size: 20px;
}

.management-right > div:nth-of-type(1) {
  width: 100%;
  height: 10.76vh;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 1.4em;
  background-image: url("../../../static/images/nav-bg.png");
  background-size: 100% 100%;
  background-repeat: no-repeat;
}

.management-right > div:nth-of-type(1) > div {
  display: flex;
  align-items: center;
}

.management-right > div:nth-of-type(1) > div > img:nth-of-type(1) {
  width: 3.2em;
}

.management-right > div:nth-of-type(1) > div > p {
  color: #fff;
  margin: 0;
  margin-right: 1em;
}

.management-right > div:nth-of-type(2) {
  height: calc(89.24vh);
  overflow: auto;
  padding: 8px 10px;
}
